
.container{
  background-color:#fbf9fe;
  -webkit-user-select:none;
  user-select:none;
  width:100%;
  overflow-x:hidden;
}

.body{
  min-height:100%;
  flex:1;
  background-color:#FBF9FE;
  font-size:16px;
  font-family:-apple-system-font,Helvetica Neue,Helvetica,sans-serif;
  overflow: auto;
  padding-bottom: 150rpx;
}

.textarea{
  height: auto;
  padding: 15px;
  width: 80%;
  background-color: #ccc;
  outline: none;
  resize: none;
  overflow: auto;
  border-radius: 5px;
  margin: 0 auto;
}

.title{
  text-align: center;
  font-size: 50rpx;
  margin-top: 10px;
}

.toolbar{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 40rpx;
}


.groupIndex{
  padding: 10px;
}

.card{
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 2px 2px 0 rgba(0,0,0,0.12);
}


.section{
  margin-top: 10px;
  text-align: center;
  margin-bottom: 40rpx;
}

.picker{
  padding:13px;
  background-color:#FFFFFF;
}

.content{
  min-height: 100%;
  margin-top: 5px;
}

.inputAll{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60rpx;
}

.flexRow{
  display: flex;
  flex-direction: row;
}

.input{
  flex-grow: 1;
  display: inline-block;
  border-bottom: 1px solid #ccc;
  margin: 10px 0 5px 5px;
}

.fLexButton{
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cardNew{
  display: flex;
  flex-direction: column;
  width: 80%;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 2px 2px 0 rgba(0,0,0,0.12);
  margin: 0 auto;
  padding: 10px;
}

.newContent{
    display: flex;
    flex-direction: column;
}

.titleIndex{
  align-self: center;
  .title;
}

.newText{
  margin-top: 20rpx;
}

.circle{
  font-size: 50rpx;
}

.banner{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  box-sizing: border-box;
  padding-right: 40rpx;
}

.nContent{
  display: flex;
  flex-direction: column;
}

.nTitle{
  align-self: center;
  .title
}


.members{
  height: 200px;
  width: 100%;
}

.searchBox{
  padding: 24px;
  display:flex;
  flex-direction:column;
}

.searchBox-input-view{
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  margin-top:20px;
}

.searchBox-input{
  padding: 4px;
  width: 85%;
}

.searchText{
  display: inline-block;
}

.searchBox-text{
  text-align: center;
  border-bottom:1px solid red;
  width:50%;
  align-self:center;
  margin-top: 20px;
}

.invite-view-title{
  display: flex;
  align-items: center;
  text-align: center;
  margin-bottom: 8px;
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

.view-top{
  padding: 10px;
}

.py{
  margin-top: -18px;
  flex: 1;
}

.cpy{
  flex: 1;
}

.view-add{
  margin-top: 10px;
  text-align: center;
}

.view-add:hover{
  background-color: #3ee9ff;
}

.button-all{
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.button-center{
  display: flex;
  justify-content: center;
  margin-bottom: 25rpx;
  margin-top: 80rpx;
}

.cancel{
  width: 100%;
  text-align: center;
}

.bottom-line{
  border-bottom: 1px solid rgb(255, 154, 154);
  padding: 2px 0;
}